.review-items {
	flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
	max-height: 22.5rem;
	max-width: 65rem;
	overflow-y: auto;

    @include respond(phone) {
        max-width: 40rem;
    }

	/* width */
    &::-webkit-scrollbar {
        width: 1.5rem;
    }
    
    /* Track */
    &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 5px $color-grey-dark-1;
        border-radius: 10px;
    }
    
    /* Handle */
    &::-webkit-scrollbar-thumb {
        background: $color-grey-dark-1;
        border-radius: 10px;
    }
}