.pagination {
    font-size: $font-size-big;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    margin-top: 5rem;

    &__label--arrow {
        font-size: 3rem;
        margin: 0;
        padding: 0;

        @include respond(phone) {
			font-size: 2rem;
            width: 5rem;
		}
    }

    &__list {
        display: flex;
        list-style: none;
        margin-top: 1rem;

        :not(:last-child) {
            margin-right: 1.5rem;
            @include respond(phone) {
                margin-right: 1rem;
            }
        }

        li {
            display: flex;
            margin-top: auto;
        }

    }
    
    &__label--number,
    &__label--selected-number {
        @include notSelectable;
        height: 2rem;   
        width: 2rem;
        padding: 2.5rem;
        border-radius: 50%;
        font-size: $font-size-big;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;

        @include respond(phone) {
			height: 1rem;   
            width: 1rem;
            padding: 1.5rem;
            font-size: $font-size-default;
		}

        &:hover,
        &:active{
            background-color: $color-primary;
            color: $color-white;
        }
    }

    &__label--selected-number {
        background-color: $color-primary;
        color: $color-white;
    }
}