.media-container {
    width: 18rem;
    height: 10rem;
    display: flex;
    align-items: flex-start;
    
    &__img,
    &__video {
        border-radius: 10px;
        width: 100%;
        height: auto;
        z-index: -100;
        overflow: hidden;
    }

    &__video {
        z-index: 0;
    }
}